import opg from 'ts/opg.ts';
import {Languages} from 'ts/util/Languages';
import Panel from 'ts/ui/Panel';
import {ListBox} from '../../../ts/ui/FormControls';

opg.api({
	getAllRole: 'role/GetAllRole',
	getMenuByRoleId: 'role/GetRoleAllMenuByRoleId',
	getFullMenuWap: 'menu/GetMenusByType?menutype=0',
	getFullMenuPc: 'menu/GetMenusByType?menutype=1',
	'addRoleMenus!post': 'role/addrolemenus',
});

const apiAddRoleMenusUrl = opg.api.addRoleMenus.toString();
const lpg = Languages.package;
const moduleName = lpg.moduleName;

const form = $('#tbSearch'), spDesc = $('#desc');
let treeWap, treePC;
let panel: Panel = opg.wrapPanel('#tbSearch', {
	title: `${moduleName}`,
	btnSearchId: null,
});

let selRole: ListBox = opg('#roleId').listBox({
	api: opg.api.getAllRole,
	autoPrependBlank: false,
	onSelect: function () {
		let data = (this as ListBox).selectedData;
		spDesc.text(data.desc || '');
		makeMenus(this.getValue());
	},
});

function makeMenus(roleId) {
	$(':checkbox').prop('checked', false);
	opg.api.getMenuByRoleId({roleId}, function (data) {
		data.map(function (val) {
			let id = val.id;
			let dom = document.getElementById(`${treeWap.treeName}Chk_${id}`);
			if (!dom) {
				dom = document.getElementById(`${treePC.treeName}Chk_${id}`);
			}
			if (dom) {
				(dom as HTMLInputElement).checked = true;
			}
		})
	});
}

$.when(
	selRole.createdPromise,
	opg.api.getFullMenuWap((data) => {
		treeWap = opg('#treeMenuWap').tree({
			data: data,
			name: 'treeWap',
			root: 'App / WeChat',
			cmd: 'checkAll',
			childSrc : 'childMenus'
		});
	}),
	opg.api.getFullMenuPc((data) => {
		treePC = opg('#treeMenuPC').tree({
			data: data,
			name: 'treePC',
			root: 'Web / back-end',
			cmd: 'checkAll',
			childSrc : 'childMenus'
		});
	}),
).then(function () {
	selRole.jq.trigger('change');
});


$('#btnSave').click(function () {
	let arr = [];
	$(':checkbox:checked', '#tb').each(function (i, elem) {
		arr.push((elem as HTMLInputElement).value);
	});

	let api = opg.api.addRoleMenus ;
	api.set('url' , `${apiAddRoleMenusUrl}/${selRole.getValue()}`) ;
	console.log(arr);
	console.log(opg.api.addRoleMenus);

	opg.api.addRoleMenus(arr , function () {

	})
});
